
import Vue from 'vue'
import NcModal from '@nextcloud/vue/dist/Components/NcModal.js'
import { getRootUrl } from '@nextcloud/router'
import FileVersion from './FileVersion.vue'
import { fetchVersions } from "../utils/fetchFileVersions.js";

export default Vue.extend({
	name: 'SelectVersionModal',

	components: {
		NcModal,
        FileVersion
	},
	props: {
		file: {
			required: true,
		},
	},
    data() {
		return {
			isActive: false,
			versions: [],
			opened: true,
			loading: false,
		}
	},
	computed: {
         orderedVersions() {
			return [...this.versions].sort((a, b) => {
				if (a.mtime === (this.file.mtime.getTime ? this.file.mtime.getTime() : this.file.mtime)) {
					return -1
				} else if (b.mtime ===(this.file.mtime.getTime ? this.file.mtime.getTime() : this.file.mtime)) {
					return 1
				} else {
					return b.mtime - a.mtime
				}
			})
		},

		initialVersionMtime() {
			return this.versions
				.map(version => version.mtime)
				.reduce((a, b) => Math.min(a, b))
		},
	},

	async beforeMount() {
		this.versions = await fetchVersions(this.file)
	},

	methods: {
		onCancel() {
			this.opened = false
			this.$emit('cancel')
		},

		versionLabel(version) {
			const label = version.label ?? ''

			if (version.mtime == (this.file.mtime.getTime ? this.file.mtime.getTime() : this.file.mtime)) {
				if (label === '') {
					return 'Current version'
				} else {
					return `${label} (Current version)`
				}
			}

			if (version.mtime == this.initialVersionMtime && label === '') {
				return 'Initial version'
			}

			return label
		},

        compareVersion({ version }) {
			const lastVersion = this.versions.findLast((v) => v.mtime == (this.file.mtime.getTime ? this.file.mtime.getTime() : this.file.mtime))
			this.$emit('select', { version, firstLabel: this.versionLabel(version), currentVersion: this.versionLabel(lastVersion) })
        },

		async fetchVersions() {
			try {
				this.loading = true
				this.versions = await fetchVersions(this.file)
			} finally {
				this.loading = false
			}
		},
	},
})
